<template>
  <div id="product-detail">
    <div class="wrap">
      <div class="pd-big">
        <img :src="detail.image" alt="">
      </div>
      <div class="pir-title">
        <h3>{{detail.title}}</h3>
        <p>2017     </p>
      </div>
      <div class="pir-table-content">
        <table class="pir-table">
          <tr>
            <td width="26%"></td>
            <td>{{detail.price}}</td>
          </tr>
          <tr>
            <td></td>
            <td><p class="pir-price-now">{{detail.deposit}}</p></td>
          </tr>
          <tr>
            <td></td>
            <td>
              <!---->
              <number-box @getcount="getSubCount"></number-box>
            </td>
          </tr>
        </table>
      </div>

      <div class="pir-btn">
        <mt-button size="large" @click="addCart"></mt-button>
      </div>

      <div class="pd-content">
        <mt-navbar v-model="selected">
          <mt-tab-item id="1"></mt-tab-item>
          <mt-tab-item id="2"></mt-tab-item>
        </mt-navbar>
        <!-- tab-container -->
        <mt-tab-container v-model="selected" :swipeable="true">
          <mt-tab-container-item id="1">
            <div class="inner-p">
              
              <br>

              //
              <br>

              P01
              <br>
              <br>
              <img src="../assets/images/index-banner1.jpg" alt="">
            </div>
          </mt-tab-container-item>
          <mt-tab-container-item id="2">
            <div class="inner-p">
              <table class="pd-desc-table">
                <tr>
                  <th colspan="2"></th>
                </tr>
                <tr>
                  <td width="30%"></td>
                  <td width="70%">SUV</td>
                </tr>
                <tr>
                  <td>**</td>
                  <td>5130*1933*1850</td>
                </tr>
                <tr>
                  <td></td>
                  <td>57SUV</td>
                </tr>
                <tr>
                  <td>mm</td>
                  <td>3075</td>
                </tr>
                <tr>
                  <td></td>
                  <td>3.0T 367 V6</td>
                </tr>
                <tr>
                  <td></td>
                  <td>500N`m@1800</td>
                </tr>
              </table>
            </div>
          </mt-tab-container-item>
        </mt-tab-container>
      </div>

    </div>

  </div>
</template>

<script>
  import mui from '../assets/lib/mui/js/mui.min'
  import '../assets/lib/mui/css/mui.css'
  import NumberBox from './common/NumberBox.vue'

  export default {
    data() {
      return {
        id: this.$route.params.id,
        selectcount: 1,
        selected: "1",
        detail: []
      }
    },
    mounted() {
      this.getDetail();

    },
    methods: {
      getDetail() {
        // 
        this.$http.get('https://raw.githubusercontent.com/ekko-xie/ekko-xie.github.io/master/api/kuagangtong-vue/productdetail/' + this.id + '.json').then(data => {
          this.detail = data.body;
        })
      },
      addCart() {
        var goodsinfo = {
          id: this.id,
          title: this.detail.title,
          image: this.detail.image,
          deposit: this.detail.deposit,
          count: this.selectcount,
          price: this.detail.price,
          selected: true
        }

        // vuexstorevuex
        this.$store.commit("addToCart", goodsinfo)
        // console.log(this.$store.state.cart)

        this.$toast({
          message: '',
          position: 'middle',
          duration: 2000
        });

      },
      getSubCount(count) {
        // 
        this.selectcount = count;
        console.log(this.selectcount)
      }
    },
    components: {
      "number-box": NumberBox
    }

  }

</script>

<style lang="less" scoped>
  #product-detail{
    background-color: #fff;

    .pd-big{
      padding-top: 20px;
    }
    .pd-big img{
      width: 100%;
    }
    .pir-title{
      margin-top: 20px;
    }
    .pir-title h3{
      font-size: 18px;
      line-height: 24px;
      color: #3f3f3f;
      font-weight: normal;
    }
    .pir-title p{
      font-size: 13px;
      color: #999999;
      line-height: 20px;
      margin-top: 10px;
    }
    .pir-table-content{
      background-color: #f1f1f1;
      padding: 10px 20px;
      border-radius: 5px;
      margin-top: 10px;
      margin-bottom: 20px;
    }
    .pir-table{
      width: 100%;
      font-size: 14px;
    }
    .pir-table td{
      padding: 8px 0;
    }
    .pir-price-now{
      color: #ff4700;
      font-size: 24px;
      font-weight: bold;
    }
    .pir-btn .mint-button{
      color: #fff;
      background-color: #ff4700;
    }
    .pd-content{
      margin-top: 40px;
      border: 1px solid #ddd;
    }
    .pd-content .mint-navbar{
      background-color: #f1f1f1;
    }
    .pd-content .mint-navbar .mint-tab-item.is-selected{
      color: #ff4700;
      border-bottom: 3px solid #ff4700;
    }
    .inner-p{
      font-size: 12px;
      line-height: 20px;
      padding: 20px;
    }
    .pd-desc-table{
      width: 100%;
      border-left: 1px solid #000;
      border-top: 1px solid #000;
    }
    .pd-desc-table th{
      padding: 5px;
      border-right: 1px solid #000;
      border-bottom: 1px solid #000;
    }
    .pd-desc-table td{
      padding: 5px;
      border-right: 1px solid #000;
      border-bottom: 1px solid #000;
    }
  }


</style>




